<template>
  <div id="app">
    <main>
      <header>
        <div id="title">Leerlinggegevens/gespreksverslag</div>
        <div id="logo">

<img src="data:image/jpeg;base64," />

</div>
        <div id="student">
          <div class="desc">naam: </div><div><strong>{{ this.$parent.llData.naam }}</strong> ({{ this.$parent.llData.groep.groep }})</div>
          <div class="desc">geboren:</div><div>{{this.niceDate(this.$parent.llData.geboren)}}</div>
          <div class="desc">adres:</div><div>{{this.$parent.llData.adres}}</div>
          <div class="desc">&nbsp;</div>
          <div class="desc"><strong>datum:</strong></div><div><strong>{{this.niceDate(new Date(),true)}}</strong></div>
          
        </div>        
      </header>
      <div class="cat" v-for="(cat, indexc) in this.$parent.scores" :key="indexc">
        <div class="cattitle">{{indexc.toUpperCase()}}</div>
        <div class="toets" v-for="(toets, indext) in cat" :key="indext">
          <div class="toetsdatum" :class="(new Date(toets.datum) > new Date($parent.llData.laatst_besproken)) ? 'hl' : ''"><span>{{shortDate(toets.datum)}}</span></div>
          <div class="toetsnaam">{{toets.toets}}</div>
          <div class="toetsscore">{{toets.score}}</div> 
          
        </div>
      </div>
      <div class="cat">
        <div class="cattitle">BESPROKEN</div>
      </div>
      <footer>
        <div></div>
        <div class="sigtitle">leerkracht:</div>
        <div class="sigtitle">ouder/verzorger:</div>
        <div class="sigtext">
          <br/>
          datum:<br/><br/>
          handtekening:<br/><br/>
        </div>
        <div class="form"></div>
        <div class="form"></div>
      </footer>
    </main>
    <!--pre>{{this.$parent.llData}}</pre-->
    <div class="print"><a href="javascript:window.print()">PRINT</a></div>
  </div>
</template>

<script>
// TODO
// + cat toupper
// + besproken deel toevoegen
// + variabele voor recente gegevens in Grist inbouwen en in script verwerken
// + datum bovenin onder llgegevens ('juli 2022')
// + print button
// + bestandsnaam bij printen meegeven
// + lettertype
// + kwaliteit logo
// - paginering met nummering?
// - uittesten in grist
// - verwijderen demo data
// - build doen en op server plaatsen

export default {
  name: 'app',
  data () {
    return {
      msg: 'Hoppa',
    }
  },
  created() {
    this.llData = this.$parent.llData
  },
  methods: {
    niceDate(d, short) { //pass true to return 'month 2022'
      d = new Date(d);
      const months = [
        "januari",
        "februari",
        "maart",
        "april",
        "mei",
        "juni",
        "juli",
        "augustus",
        "september",
        "oktober",
        "november",
        "december"
        ]
      if (short) {
        return months[d.getMonth()]+" "+d.getFullYear();
      } else {
        return d.getDate()+" "+months[d.getMonth()]+" "+d.getFullYear();
      }
    },
    shortDate(d) {
      d = new Date(d);
      let m = d.getMonth()+1;
      let pre = "";
      if (m.toString().length < 2) {
        pre="0";
      }
      return `${pre}`+m+"/"+d.getFullYear() % 100;
    },
  }
}
</script>

<style>
:root {
  --greyback:  rgb(231, 231, 231);
  --darkgrey:  rgb(150, 150, 150);
  --middlegrey: rgb(187, 187, 187);
}
#app {
  
  /* font-family: 'Roboto', Helvetica, Arial, sans-serif; */
   font-family:Thesis,Thesis Plus Kyrillic,Helvetica,Arial,sans-serif;
  font-weight:400;
  font-size: .8em;
  /*-webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;*/

}
main {
  display: grid;
  grid-template-columns: 1fr 8fr 12fr;
  align-items: left;
  column-gap:0.5em;
  row-gap:0.5em;
  padding:.5em;
}
main * {
  justify-content: left;
 
}
header {
  grid-column-start: 1;
  grid-column-end: 4;
  display: grid;
  grid-template-columns: 3fr 2fr;
  row-gap: .5em;
  column-gap: .5em;
  padding: .5em;
  margin-bottom: 1em;
}
#logo {
  justify-content: right;
  text-align:right;
}
#logo img {
  height:4em;
}

#student {
  display:grid;
  grid-template-columns: 3fr 30fr;
  row-gap: .1em;
  column-gap: .5em;
}
.desc {
  grid-column-start: 1;
}
.cat {
  grid-column-start: 1;
  grid-column-end: 4;
  display:grid;
  grid-template-columns: 1fr 5fr 15fr;
  row-gap: .0em;
  column-gap: .5em;
}
.cattitle {
  background-color: var(--greyback);
  grid-column-start: 1;
  grid-column-end: 4;
  padding: .5em;
  margin-bottom: .5em;
  font-weight: bold;
  font-size: 1em;
  padding-top: 0.2em;
  padding-bottom: 0.2em;
}
.toets {
  grid-column-start: 1;
  grid-column-end: 4;
  display:grid;
  grid-template-columns: 1fr 5fr 15fr;
  column-gap: .5em;
  row-gap: 0em;
  padding: 0em 0em .0em 0em;

}
.toets * {
  padding: .2em;
}
.toetsdatum {
  color: var(--darkgrey);
}
.hl {
  border-left: var(--middlegrey) .2em solid;
  padding: .3em;
  padding-left: .1em;
  /* padding-left: 0em; */
  /* background-color: var(--middlegrey);
  color:white;
  border-radius: .3em;
  padding-top: .1em;
  padding-bottom: .1em; */

}
footer {
      page-break-inside: avoid;
    break-inside: avoid;
  grid-column-start: 1;
  grid-column-end: 4;
  display:grid;
  grid-template-columns: 3fr 8fr 8fr;
  gap: .5em;
  padding: .5em;
  background-color: var(--greyback);
}
.sigtitle {
  font-style: italic;
}
.sigtext {
  height: 9em;
}
.form {
  background-color: white;
}
#title {
  font-size: 1.5em;
  font-weight: bold;

}
div.print {
	position: fixed;
	right: 0;
	top: 0;
}
div.print a {
	background: #C83200;
	display: block;
	font-size: 1.0em;
	color: white;
	text-decoration: none;
	padding: 0.25em;
	padding-bottom: 0.5em;
	padding-left: 0.5em;
	border-bottom-left-radius: 0.5em;
	text-transform: uppercase;
}
@media print {
  div.print {
    display: none;
  }
  .cat {
    display:contents;
  }
  .toets {
    page-break-inside: avoid;
    break-inside: avoid;
    padding-top:0em;
    padding-bottom:0em;
  }
  .toets * {
    padding-top:0em;
    padding-bottom: 0em;
  }
  .cattitle {
    page-break-inside: avoid;
    margin-bottom:0em;
  }
  footer, footer * {
    page-break-inside: avoid;
    break-inside: avoid;
  }
}
@page {
  size:A4;
  margin:15mm;

}



</style>



